<template>
  <div class="mybookshelf-view">
    <nav-menu :navMenuList="navMenuList"
    :activeIndex="activeIndex"
    @handleSelect="handleSelect"></nav-menu>
    <div class="search-panel">
      <div class="allPage">
        共100本
      </div>
      <div class="d-search">
        <input type="text" placeholder="请输入书籍名称">
        <button @click="cliclSearch">
          <i class="el-icon-search"></i>
        </button>
      </div>
    </div>
    <ul class="list-view">
      <li v-for="(item,index) in list" :key="index">
        <div class="left">
          <img :src="item.imgUrl" alt="">
        </div>
        <div class="right">
          <strong>{{item.name}}</strong>
          <p class="p-money">
            ￥{{item.money}}
            <span>
              定价：￥{{item.beforeMoney}}
            </span>
          </p>
          <p class="p-name">{{item.author}}</p>
          <button class="read-btn">继续阅读</button>
          <div class="btns-d">
            <el-button @click="shoppingCart(item.isShoppingCart)" type="text">
              <i :class="item.isShoppingCart ? 'el-icon-circle-close' : 'el-icon-circle-check'"></i>
            </el-button>
            <el-button @click="collection" type="text"><i class="el-icon-star-on"></i></el-button>
          </div>
        </div>
      </li>
    </ul>
    <div class="pagination">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage3"
        :page-size="100"
        layout="prev, pager, next, jumper"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import NavMenu from '@/base/navmenu/navmenu'
export default {
  name: '',
  components: {
    'nav-menu': NavMenu
  },
  data () {
    return {
      currentPage3: 5,
      activeIndex: '0',
      navMenuList: [{
        text: '我的收藏',
        pathName: ''
      }],
      list: [{
        id: '1',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        beforeMoney: '50.22',
        money: '40.11',
        isShoppingCart: true
      }, {
        id: '2',
        imgUrl: require('@/assets/images/2.png'),
        name: '三国演义',
        author: '我写的',
        beforeMoney: '50.22',
        money: '40.11',
        isShoppingCart: false
      }]
    }
  },
  methods: {
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
    },
    cliclSearch () {
      alert('搜索')
    },
    shoppingCart (state) { // 加入购物车
      alert(state)
    },
    collection () { // 收藏
      alert('收藏')
    },
    handleSelect (key) {
      this.activeIndex = key
      // console.log(this.navMenuList[key].text)
    }
  }
}
</script>
<style lang="less" scoped>
  .mybookshelf-view{
    position: relative;
  }
  .pagination{
    text-align: right;
    padding: 50px 0;
  }
  .search-panel{
    height: 30px;
    width: 860px;
    position: absolute;
    right: 0;
    top: -2px;
    display: flex;
    line-height: 30px;
    .allPage{
      flex: 1;
      color: #7B6EA3;
      font-size: 14px;
      font-weight: 600;
    }
    .d-search{
      line-height: 20px;
      button{
        background-color: #B7151E;
        border: 0;
        outline: none;
        cursor: pointer;
        color: white;
        padding: 0 10px;
        height: 100%;
        float: right;
        font-size: 16px;
      }
      input{
        width: 200px;
        outline: none;
        border: 2px solid #B7151E;
        height: 24px;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
      }
    }
  }
  .list-view{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    li{
      max-width: 50%;
      min-width: 50%;
      display: flex;
      padding: 40px 0;
      border-bottom: 1px solid #DBDBDB;
      p{
        padding: 0;
        margin: 0;
        font-size: 14px;
        padding-bottom: 10px;
        color: #9F9F9F;
      }
      .left{
        padding: 0 20px;
        img{
          width: 150px;
          height: 180px;
        }
      }
      .right{
        .star{
          padding-bottom: 10px;
        }
        .p-money{
          padding-top: 10px;
          font-weight: 600;
          color: red;
          font-size: 18px;
          span{
            font-size: 14px;
            color: #A0A0A0;
            font-weight: 200;
            text-decoration: line-through;
          }
        }
        .read-btn{
          width: 100px;
          height: 28px;
          background-color: #B7151E;
          color: white;
          outline: none;
          border: 0;
          border-radius: 6px;
          cursor: pointer;
        }
        .btns-d{
          padding-top: 20px;
          button{
            width: 40px;
            font-size: 24px;
            padding: 0;
            color: #B7151E;
          }
        }
      }
    }
  }
</style>
<style>
.el-pagination.is-background .el-pager li:not(.disabled).active{
    background: #F76B6E !important;
  }
</style>



